"use client"

import { useEffect, useState } from "react"
import { collection, getDocs, addDoc, updateDoc, deleteDoc, doc, query, orderBy } from "firebase/firestore"
import { db } from "@/lib/firebase"
import type { Property } from "@/lib/types"
import { AuthGuard } from "@/components/admin/auth-guard"
import { EnhancedPropertyForm } from "@/components/admin/enhanced-property-form"
import { SiteSettingsForm } from "@/components/admin/site-settings"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { useAuth } from "@/hooks/use-auth"
import { Plus, Edit, Trash2, LogOut, Loader2, Settings, Home, MapPin, Search } from "lucide-react"
import { Input } from "@/components/ui/input"
import { searchProperties } from "@/lib/search-utils"
import { Bed, Bath, Square } from "lucide-react"
import Image from "next/image"
import { siteConfig } from "@/lib/site-config"

export default function AdminPage() {
  const { logout } = useAuth()
  const [properties, setProperties] = useState<Property[]>([])
  const [loading, setLoading] = useState(true)
  const [showForm, setShowForm] = useState(false)
  const [editingProperty, setEditingProperty] = useState<Property | null>(null)
  const [activeTab, setActiveTab] = useState("properties")
  const [searchTerm, setSearchTerm] = useState("")
  const [filteredAdminProperties, setFilteredAdminProperties] = useState<Property[]>([])

  useEffect(() => {
    fetchProperties()
  }, [])

  useEffect(() => {
    if (searchTerm.trim()) {
      setFilteredAdminProperties(searchProperties(properties, searchTerm))
    } else {
      setFilteredAdminProperties(properties)
    }
  }, [properties, searchTerm])

  const fetchProperties = async () => {
    setLoading(true)
    try {
      const q = query(collection(db, "properties"), orderBy("createdAt", "desc"))
      const querySnapshot = await getDocs(q)
      
      const propertiesData = querySnapshot.docs.map((doc) => {
        const data = doc.data();
        return {
            id: doc.id,
            ...data,
            createdAt: data.createdAt?.toDate ? data.createdAt.toDate() : new Date(),
            updatedAt: data.updatedAt?.toDate ? data.updatedAt.toDate() : new Date(),
        }
      }) as Property[]

      setProperties(propertiesData)
    } catch (error) {
      console.error("Erro ao buscar propriedades:", error)
    } finally {
      setLoading(false)
    }
  }

  const handleSubmit = async (propertyData: Omit<Property, "id">) => {
    try {
      if (editingProperty && editingProperty.id) {
        await updateDoc(doc(db, "properties", editingProperty.id), propertyData)
      } else {
        await addDoc(collection(db, "properties"), propertyData)
      }
      await fetchProperties()
      setShowForm(false)
      setEditingProperty(null)
    } catch (error) {
      console.error("Erro ao salvar:", error)
      alert("Erro ao salvar.")
    }
  }

  const handleEdit = (property: Property) => {
    setEditingProperty(property)
    setShowForm(true)
  }

  const handleDelete = async (propertyId: string) => {
    if (confirm("Tem certeza?")) {
      try {
        await deleteDoc(doc(db, "properties", propertyId))
        await fetchProperties()
      } catch (error) {
        console.error("Erro ao excluir:", error)
      }
    }
  }

  const formatPrice = (price: number) => {
    return new Intl.NumberFormat("pt-BR", { style: "currency", currency: "BRL" }).format(price)
  }

  return (
    <AuthGuard>
      <div className="min-h-screen bg-background text-foreground">
        
        {/* Header Admin */}
        <header className="sticky top-0 z-30 w-full border-b border-border bg-card/95 backdrop-blur supports-[backdrop-filter]:bg-card/60">
          <div className="container mx-auto px-4 h-16 flex items-center justify-between">
            <div className="flex items-center gap-2">
               <div className="relative h-10 w-10 overflow-hidden">
                  <Image 
                    src={siteConfig.logoUrl} 
                    alt={siteConfig.name} 
                    fill 
                    className="object-conteiner"
                  />
                </div>
               <span className="font-bold text-lg hidden md:block">Painel Administrativo</span>
            </div>
            <Button variant="ghost" onClick={logout} className="text-muted-foreground hover:text-foreground hover:bg-secondary">
              <LogOut className="h-4 w-4 mr-2" />
              Sair
            </Button>
          </div>
        </header>

        <div className="container mx-auto px-4 py-8">
          <Tabs value={activeTab} onValueChange={setActiveTab} className="space-y-6">
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
                <TabsList className="bg-card border border-border p-1">
                  <TabsTrigger value="properties" className="data-[state=active]:bg-primary data-[state=active]:text-primary-foreground">
                    <Home className="h-4 w-4 mr-2" /> Imóveis
                  </TabsTrigger>
                  <TabsTrigger value="settings" className="data-[state=active]:bg-primary data-[state=active]:text-primary-foreground">
                    <Settings className="h-4 w-4 mr-2" /> Configurações
                  </TabsTrigger>
                </TabsList>

                {!showForm && activeTab === "properties" && (
                    <Button onClick={() => { setEditingProperty(null); setShowForm(true) }} className="bg-primary text-primary-foreground hover:bg-primary/90">
                      <Plus className="h-4 w-4 mr-2" /> Novo Imóvel
                    </Button>
                )}
            </div>

            <TabsContent value="properties" className="space-y-6">
              {showForm ? (
                <EnhancedPropertyForm
                  property={editingProperty || undefined}
                  onSubmit={handleSubmit}
                  onCancel={() => { setShowForm(false); setEditingProperty(null) }}
                />
              ) : (
                <>
                  {/* Barra de Busca Admin */}
                  <div className="relative w-full max-w-md mb-6">
                    <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
                    <Input
                      value={searchTerm}
                      onChange={(e) => setSearchTerm(e.target.value)}
                      placeholder="Buscar por endereço, bairro ou valor..."
                      className="bg-card border-border text-foreground pl-10 h-11"
                    />
                  </div>

                  {loading ? (
                    <div className="flex items-center justify-center py-20">
                      <Loader2 className="h-8 w-8 animate-spin text-primary" />
                    </div>
                  ) : filteredAdminProperties.length === 0 ? (
                    <div className="text-center py-20 border border-dashed border-border rounded-lg bg-card/50">
                      <p className="text-muted-foreground text-lg">Nenhum imóvel encontrado.</p>
                    </div>
                  ) : (
                    <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-6">
                      {filteredAdminProperties.map((property) => (
                        <Card key={property.id} className="bg-card overflow-hidden  transition-all flex flex-col group">
                          <div className="relative h-48 bg-secondary">
                            <img
                              src={property.coverPhoto || "/placeholder.svg"}
                              alt={property.location}
                              className="w-full h-full object-cover transition-transform group-hover:scale-105 duration-500"
                            />
                            <div className="absolute top-2 right-2 flex gap-2">
                                <Badge className={property.status === 'available' ? 'bg-green-600 text-white' : 'bg-red-600 text-white'}>
                                    {property.status === 'available' ? 'Ativo' : property.status === 'sold' ? 'Vendido' : 'Alugado'}
                                </Badge>
                            </div>
                            <div className="absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black/80 to-transparent p-3">
                                <div className="text-xl font-bold text-white">{formatPrice(property.price)}</div>
                            </div>
                          </div>

                          <CardContent className="p-4 flex flex-col gap-3 flex-grow">
                             <div className="flex items-start gap-2 text-sm text-muted-foreground min-h-[2.5rem]">
                                <MapPin className="mt-0.5 h-4 w-4 shrink-0 text-primary" />
                                <span className="line-clamp-2 leading-tight">
                                    {property.location}
                                </span>
                             </div>

                             <div className="flex items-center justify-between text-sm text-muted-foreground border-t border-border pt-3 mt-auto">
                                <div className="flex items-center gap-1"><Bed className="h-4 w-4"/> {property.bedrooms}</div>
                                <div className="flex items-center gap-1"><Bath className="h-4 w-4"/> {property.bathrooms}</div>
                                <div className="flex items-center gap-1"><Square className="h-4 w-4"/> {property.area} {property.areaUnit}</div>
                            </div>

                            <div className="grid grid-cols-2 gap-2 mt-2 pt-2">
                              <Button variant="outline" size="sm" className="border-border hover:bg-secondary hover:text-foreground" onClick={() => handleEdit(property)}>
                                <Edit className="h-4 w-4 mr-2" /> Editar
                              </Button>
                              <Button variant="destructive" size="sm" className="bg-destructive/60 text-destructive-foreground hover:bg-destructive/50 " onClick={() => handleDelete(property.id!)}>
                                <Trash2 className="h-4 w-4 mr-2" /> Excluir
                              </Button>
                            </div>
                          </CardContent>
                        </Card>
                      ))}
                    </div>
                  )}
                </>
              )}
            </TabsContent>

            <TabsContent value="settings">
              <SiteSettingsForm />
            </TabsContent>
          </Tabs>
        </div>
      </div>
    </AuthGuard>
  )
}