"use client"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Switch } from "@/components/ui/switch"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { X, Loader2, UploadCloud, AlertCircle } from "lucide-react"
import { ref, uploadBytes, getDownloadURL } from "firebase/storage"
import { storage } from "@/lib/firebase"
import type { Property } from "@/lib/types/property"
import { useMediaUpload } from "@//hooks/use-media-upload" // Vamos criar esse hook no passo 3 se não tiver

interface EnhancedPropertyFormProps {
  property?: Property
  onSubmit: (property: Omit<Property, "id">) => Promise<void>
  onCancel: () => void
}

export function EnhancedPropertyForm({ property, onSubmit, onCancel }: EnhancedPropertyFormProps) {
const [formData, setFormData] = useState<Pick<Property,
  | "location"
  | "neighborhood"
  | "street"
  | "city"
  | "price"
  | "financeable"
  | "propertyType"
  | "status"
  | "description"
  | "bedrooms"
  | "bathrooms"
  | "area"
  | "areaUnit"
  | "videoUrl"
>>({
  location: property?.location || "",
  neighborhood: property?.neighborhood || "",
  street: property?.street || "",
  city: property?.city || "",
  price: property?.price || 0,
  financeable: property?.financeable || false,
  propertyType: property?.propertyType || "sale",
  status: property?.status || "available",
  description: property?.description || "",
  bedrooms: property?.bedrooms || 0,
  bathrooms: property?.bathrooms || 0,
  area: property?.area || 0,
  areaUnit: property?.areaUnit || "m²",
  videoUrl: property?.videoUrl || ""
})

  // Fotos
  const [photos, setPhotos] = useState<File[]>([])
  const [existingPhotos, setExistingPhotos] = useState<string[]>(property?.photos || [])
  const [coverPhotoIndex, setCoverPhotoIndex] = useState(0)
  
  // Estados
  const [isSaving, setIsSaving] = useState(false)
  const [uploadProgress, setUploadProgress] = useState("")
  const [error, setError] = useState("")

  // Se já tiver fotos, ajusta o index da capa
  useEffect(() => {
    if (property?.coverPhoto && property.photos) {
      const index = property.photos.indexOf(property.coverPhoto)
      if (index >= 0) setCoverPhotoIndex(index)
    }
  }, [property])

  // Upload manual simples (sem compressão complexa por enquanto para evitar erros de dependência)
  const uploadPhotosToFirebase = async (): Promise<string[]> => {
    const urls: string[] = []
    for (let i = 0; i < photos.length; i++) {
      setUploadProgress(`Enviando foto ${i + 1} de ${photos.length}...`)
      const file = photos[i]
      const timestamp = Date.now()
      const safeName = file.name.replace(/[^a-zA-Z0-9.]/g, "_")
      const storageRef = ref(storage, `properties/${timestamp}_${safeName}`)
      const snapshot = await uploadBytes(storageRef, file)
      const url = await getDownloadURL(snapshot.ref)
      urls.push(url)
    }
    return urls
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsSaving(true)
    setError("")

    try {
      // 1. Upload das novas fotos
      let finalPhotos = [...existingPhotos]
      if (photos.length > 0) {
        const newUrls = await uploadPhotosToFirebase()
        finalPhotos = [...finalPhotos, ...newUrls]
      }

      if (finalPhotos.length === 0) throw new Error("Adicione pelo menos uma foto.")

      // 2. Definir capa
      const safeCoverIndex = coverPhotoIndex < finalPhotos.length ? coverPhotoIndex : 0

      // 3. Construir endereço
      const displayLocation = formData.location || `${formData.neighborhood}, ${formData.city}`

      // 4. Montar objeto final
      const propertyData: Omit<Property, "id"> = {
        ...formData,
        location: displayLocation,
        // Garantir números
        price: Number(formData.price),
        bedrooms: Number(formData.bedrooms),
        bathrooms: Number(formData.bathrooms),
        area: Number(formData.area),
        // Garantir tipos literais
        propertyType: formData.propertyType as "sale" | "rent",
        status: formData.status as "available" | "sold" | "rented",
        areaUnit: formData.areaUnit as "m²" | "ha",
        
        photos: finalPhotos,
        coverPhoto: finalPhotos[safeCoverIndex],
        createdAt: property?.createdAt || new Date(),
        updatedAt: new Date()
      }

      await onSubmit(propertyData)

    } catch (err: any) {
      console.error(err)
      setError(err.message || "Erro ao salvar.")
    } finally {
      setIsSaving(false)
      setUploadProgress("")
    }
  }

  // Adicionar fotos à fila
  const handleFileSelect = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      setPhotos([...photos, ...Array.from(e.target.files)])
    }
  }

  return (
    <Card className="border-gray-800 bg-gray-900">
      <CardHeader>
        <CardTitle className="text-white">{property ? "Editar Imóvel" : "Novo Imóvel"}</CardTitle>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-6">
          
          {error && (
             <Alert variant="destructive" className="border-red-900 bg-red-900/20 text-red-200">
               <AlertCircle className="h-4 w-4"/>
               <AlertDescription>{error}</AlertDescription>
             </Alert>
          )}

          {/* UPLOAD DE FOTOS */}
          <div className="space-y-4 border-b border-gray-800 pb-6">
            <Label className="text-gray-300">Fotos</Label>
            <div className="grid grid-cols-3 md:grid-cols-5 gap-4">
              {/* Existentes */}
              {existingPhotos.map((url, idx) => (
                <div key={`exist-${idx}`} className="relative aspect-square group">
                  <img src={url} className="w-full h-full object-cover rounded-md border border-gray-700"/>
                  <button type="button" onClick={() => setExistingPhotos(p => p.filter((_, i) => i !== idx))} className="absolute top-1 right-1 bg-red-600 text-white p-1 rounded-full opacity-0 group-hover:opacity-100 transition-opacity"><X size={12}/></button>
                  <button type="button" onClick={() => setCoverPhotoIndex(idx)} className={`absolute bottom-1 left-1 text-[10px] px-2 py-0.5 rounded ${coverPhotoIndex === idx ? "bg-green-600 text-white" : "bg-gray-800 text-gray-300"}`}>
                    {coverPhotoIndex === idx ? "Capa" : "Definir Capa"}
                  </button>
                </div>
              ))}
              {/* Novas */}
              {photos.map((file, idx) => (
                <div key={`new-${idx}`} className="relative aspect-square">
                  <img src={URL.createObjectURL(file)} className="w-full h-full object-cover rounded-md border border-blue-500"/>
                  <button type="button" onClick={() => setPhotos(p => p.filter((_, i) => i !== idx))} className="absolute top-1 right-1 bg-red-600 text-white p-1 rounded-full"><X size={12}/></button>
                  <span className="absolute bottom-1 right-1 bg-blue-600 text-white text-[10px] px-1 rounded">Nova</span>
                </div>
              ))}
              {/* Botão */}
              <label className="aspect-square flex flex-col items-center justify-center border-2 border-dashed border-gray-700 rounded-md cursor-pointer hover:border-gray-500 hover:bg-gray-800 transition-colors">
                <UploadCloud className="text-gray-400 mb-2"/>
                <span className="text-xs text-gray-400">Adicionar</span>
                <input type="file" multiple accept="image/*" className="hidden" onChange={handleFileSelect} />
              </label>
            </div>
          </div>

          {/* ENDEREÇO */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label className="text-gray-300">Bairro</Label>
              <Input value={formData.neighborhood} onChange={e => setFormData({...formData, neighborhood: e.target.value})} className="bg-gray-800 border-gray-700 text-white" placeholder="Ex: Centro"/>
            </div>
            <div className="space-y-2">
              <Label className="text-gray-300">Cidade</Label>
              <Input value={formData.city} onChange={e => setFormData({...formData, city: e.target.value})} className="bg-gray-800 border-gray-700 text-white" placeholder="Ex: Porteirinha"/>
            </div>
            <div className="space-y-2 col-span-2">
              <Label className="text-gray-300">Endereço/Rua (Opcional)</Label>
              <Input value={formData.street} onChange={e => setFormData({...formData, street: e.target.value})} className="bg-gray-800 border-gray-700 text-white"/>
            </div>
          </div>

          {/* DETALHES */}
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            <div className="space-y-2 col-span-2">
              <Label className="text-gray-300">Preço (R$)</Label>
              <Input type="number" value={formData.price} onChange={e => setFormData({...formData, price: Number(e.target.value)})} className="bg-gray-800 border-gray-700 text-white text-lg font-bold"/>
            </div>
            <div className="space-y-2">
              <Label className="text-gray-300">Tipo</Label>
              <Select value={formData.propertyType} onValueChange={v => setFormData({...formData, propertyType: v as any})}>
                <SelectTrigger className="bg-gray-800 border-gray-700 text-white"><SelectValue/></SelectTrigger>
                <SelectContent>
                  <SelectItem value="sale">Venda</SelectItem>
                  <SelectItem value="rent">Aluguel</SelectItem>
                </SelectContent>
              </Select>
            </div>
            <div className="space-y-2 flex flex-col justify-end pb-3">
              <div className="flex items-center space-x-2">
                <Switch checked={formData.financeable} onCheckedChange={c => setFormData({...formData, financeable: c})}/>
                <Label className="text-gray-300">Financiável</Label>
              </div>
            </div>
          </div>

          {/* CARACTERISTICAS */}
          <div className="grid grid-cols-3 gap-4">
             <div><Label className="text-gray-300">Quartos</Label><Input type="number" value={formData.bedrooms} onChange={e => setFormData({...formData, bedrooms: Number(e.target.value)})} className="bg-gray-800 border-gray-700 text-white"/></div>
             <div><Label className="text-gray-300">Banheiros</Label><Input type="number" value={formData.bathrooms} onChange={e => setFormData({...formData, bathrooms: Number(e.target.value)})} className="bg-gray-800 border-gray-700 text-white"/></div>
             <div>
               <Label className="text-gray-300">Área</Label>
               <div className="flex gap-2">
                 <Input type="number" value={formData.area} onChange={e => setFormData({...formData, area: Number(e.target.value)})} className="bg-gray-800 border-gray-700 text-white"/>
                 <Select value={formData.areaUnit} onValueChange={(v) => setFormData({ ...formData, areaUnit: v as Property["areaUnit"] })}>
                    <SelectTrigger className="w-20 bg-gray-800 border-gray-700 text-white"><SelectValue/></SelectTrigger>
                    <SelectContent><SelectItem value="m²">m²</SelectItem><SelectItem value="ha">ha</SelectItem></SelectContent>
                 </Select>
               </div>
             </div>
          </div>

          <div className="space-y-2">
            <Label className="text-gray-300">Descrição</Label>
            <Textarea value={formData.description} onChange={e => setFormData({...formData, description: e.target.value})} className="bg-gray-800 border-gray-700 text-white min-h-[100px]"/>
          </div>

          <div className="flex justify-end gap-3 pt-4 border-t border-gray-800">
            <Button type="button" variant="ghost" onClick={onCancel} className="text-gray-300">Cancelar</Button>
            <Button type="submit" className="bg-green-600 hover:bg-green-700 text-white" disabled={isSaving}>
              {isSaving ? <><Loader2 className="mr-2 h-4 w-4 animate-spin"/> {uploadProgress || "Salvando..."}</> : "Salvar Imóvel"}
            </Button>
          </div>
        </form>
      </CardContent>
    </Card>
  )
}